import React from 'react'


//Messages data
import usersData from '..//..//data/usersData';
import userMessageData from '..//..//data/usersMessageData';
import {withRouter} from 'react-router-dom';

//Message child column page
import MessageColumn from './MessageColumn/MessageColumn';
import UsersColumn from './UsersColumn/UsersColumn';

const MessagesPage = (props) => {
    let locationPathId = props.location.pathname.split('/')[2],
        locationFilterData = usersData.filter(el=>el.id == locationPathId)
    
    // console.log(locationFilterData)
    
    return (
        <div className="content-messages container-C">
            <UsersColumn parentPathName={props.match.path} data={usersData} />
            <MessageColumn    data={locationFilterData}/>
        </div>
    )
}
// userImage={props.data.image}


export default withRouter(MessagesPage);